.page-content .article-content .react-pdf__Document {
  padding-bottom: 56.25%;
}
.page-content .article-content .react-pdf__Page__textContent,
.page-content .article-content .react-pdf__Page__annotations {
  display: none;
}
.page-content .article-content .react-pdf__Page__canvas {
  margin: 0 auto;
  max-width: 100%;
  height: unset !important;
  position: absolute;
}
.page-content .article-content .ant-pagination {
  margin-top: 15px;
  text-align: center;
}
.page-content .article-content .ant-pagination li {
  white-space: unset;
  margin-left: unset;
}
.page-content .article-content .ant-pagination .anticon {
  font-size: 12px;
}
